@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  font-family: "Roboto Condensed", sans-serif !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.18 solid #f9ab00;
}


nav {
  z-index: 2;
  opacity: 0.9;
}
.logo {
  width: 4rem;
}

nav a.nav-link {
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  border-bottom: 0.18rem solid rgba(255, 255, 255, 0);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 4rem;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-bottom: 0.18rem solid #f9ab00;
}

.navbar-nav > li > a:hover {
  border-bottom: 0.18rem solid #f9ab00;
}

.header-warper {
  position: relative;
  background: url("https://cdn.pixabay.com/photo/2015/07/02/10/13/sky-828648_960_720.jpg")
    no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  background-color: blue;
  background-blend-mode: difference;
}

.main-info {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  z-index: 3;
}

.main-info h1 {
  color: #f9ab00;
  text-transform: uppercase;
}

/* ------ TYPED TEXT ------ */

.typed-text {
  font-size: 2rem;
  color: #fff;
}

/* ------ HEADER MAIN OFFER BUTTON ------ */

.btn-main-offer {
  border: 1px solid #ef4035;
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.62rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #e22215;
  color: #fff;
  cursor: pointer;
}

/* ------ PARTICLES-JS ------ */
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

.photo-warp {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid #f9ab00;
}

.about-heading {
  color: #f9ab00;
  text-transform: uppercase;
}

@media (max-width: 468px) {
  .profile-img {
    width: 10rem !important;
    height: 10rem !important;
  }
}

.services {
  text-align: center;
  padding-bottom: 2rem;
  background-color: #f4f4f4;
}

.services h1 {
  color: rgb(52, 58, 64);
  text-transform: uppercase;
}

.services .circle {
  position: relative;
  margin: 0.3rem auto;
  background: #f9ab00;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
}

.services .icon {
  font-size: 1.4rem;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-bottom: 0.3rem solid #f9ab00;
  padding: 0.6rem;
  margin-bottom: 1.8rem;
  background: #fff;
  transition: 0.5s ease-in-out;
}

.services .box:hover {
  background: #f9ab00;
  border-bottom: 0.3rem solid rgb(52, 58, 64);
}

.services .box:hover .icon {
  color: rgb(52, 58, 64);
}

.services .box:hover .circle {
  background: #fff;
}

.services .box:hover p {
  color: #fff;
}

/* colors:
grey: #ccd1d9
dark grey: #666666

*/

.experience {
  padding: 0.7rem;
}

.experience-warpper {
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #f9ab00;
}

.experience-warpper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background-color: #ccd1d9;
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(52, 58, 64);
  background: #f9ab00;
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: rgb(52, 58, 64);
  direction: ltr;
}

.timeline-content {
  margin-top: 0.3125rem;
  color: #e22215;
  font-weight: 900;
}

@media (max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }

  .experience-warpper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    width: 100%;
    float: none;
  }

  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

.portfolio-wrapper {
  background: #f4f4f4;
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #f9ab00;
}

.portfolio-image {
  width: 15rem;
  height: 10rem;
  border: 1px solid #f9ab00;
  padding: 0.5rem 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  width: 15rem;
  height: 10rem;
  top: 0;
  border: 1px solid #ef4035;
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 4rem;
  color: #f9ab00;
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3 ease-in-out;
  cursor: pointer;
}

.popupbox-titleBar {
  margin-top: 5rem !important;
}

/* ------OVERFLOW BOX------ */

.portfolio-image-popubox {
  width: 35rem;
  padding: 0 0.05rem;
}

.hyper-link {
  cursor: pointer;
  color: #f9ab00;
}
.hyper-link:hover {
  color: #ef4035;
}

@media (max-width: 768px) {
  .portfolio-image-popubox {
    width: 100%;
  }
}

@media (max-height: 570px) {
  .portfolio-wrapper {
    height: 100% !important;
  }

  .portfolio-image-popubox {
    width: 50%;
  }
}

@media (max-height: 280px) {
  .portfolio-wrapper {
    height: 90% !important;
  }

  .portfolio-image-popubox {
    width: 30%;
  }
}

.my-carousel {
  color: #fff;
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
