  height: 10rem !important;
  border-radius: 50%;
  margin-bottom: 2%;
}

.my-carousel h3 {
  color: #f9ab00;
  font-weight: 600;
  text-transform: uppercase;
}

.my-carousel p {
  color: #ccd1d9;
}

.carousel .slide {
  background: transparent !important;
  height: 100%;
